import React from "react";
import classNames from "./WT.module.css";

const WT = (props) => {
  return (
    <div className={classNames.container}>
      <div className={classNames.inner}>
        <div className={classNames.hero_text}>
          <h1>Welcome to Multiplus</h1>
        </div>
        {props.children}
      </div>
    </div>
  );
};

export default WT;
