import React, { useState } from "react";
import axios from "axios";
import FormLoader from "../components/FormLoader/FormLoader";
import swal from "sweetalert";
import { API } from "../../config";
import WT from "../components/WelcomeTemplate/WT";
import classNames from "./Verify.module.css";

const Verify = ({ history }) => {
  const [loading, setLoading] = useState(false);
  const verify = () => {
    setLoading(true);
    const urlParams = new URLSearchParams(window.location.search);
    axios
      .get(`${API}/active-account/${urlParams.get("token")}`)
      .then((response) => {
        setLoading(false);
        localStorage.setItem("authToken", JSON.stringify(response.data.token));
        localStorage.setItem("user", JSON.stringify(response.data.data.user));
        swal(
          "Activation success!",
          "You will now redirect to the App page!",
          "success",
          {
            button: "Okay",
          }
        ).then(() => {
          history.push("/app");
        });
      })
      .catch((err) => {
        setLoading(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };
  return (
    <WT>
      <div className={classNames.verify_email}>
        <button onClick={verify}>
          {loading ? <FormLoader /> : "Active account"}
        </button>
      </div>
    </WT>
  );
};

export default Verify;
